<template><div><h1 id="spring-mvc-详解" tabindex="-1"><a class="header-anchor" href="#spring-mvc-详解"><span>Spring MVC 详解</span></a></h1>
<h2 id="_1-spring-mvc-简介" tabindex="-1"><a class="header-anchor" href="#_1-spring-mvc-简介"><span>1. Spring MVC 简介</span></a></h2>
<p>Spring MVC 是 Spring Framework 的一个模块，实现了 Model-View-Controller (MVC) 架构，用于开发灵活、松耦合的 Web 应用程序。</p>
<h3 id="_1-1-主要特点" tabindex="-1"><a class="header-anchor" href="#_1-1-主要特点"><span>1.1 主要特点</span></a></h3>
<ul>
<li>清晰的角色划分</li>
<li>灵活的配置</li>
<li>强大的功能扩展</li>
<li>优秀的 RESTful 支持</li>
<li>简单的异常处理</li>
<li>方便的表单处理和数据验证</li>
</ul>
<h2 id="_2-spring-mvc-架构" tabindex="-1"><a class="header-anchor" href="#_2-spring-mvc-架构"><span>2. Spring MVC 架构</span></a></h2>
<h3 id="_2-1-核心组件" tabindex="-1"><a class="header-anchor" href="#_2-1-核心组件"><span>2.1 核心组件</span></a></h3>
<ol>
<li><strong>DispatcherServlet</strong>：前端控制器，处理所有请求</li>
<li><strong>HandlerMapping</strong>：处理器映射，映射请求到处理器</li>
<li><strong>Controller</strong>：处理器，处理业务逻辑</li>
<li><strong>ModelAndView</strong>：封装模型和视图信息</li>
<li><strong>ViewResolver</strong>：视图解析器，解析视图</li>
</ol>
<h3 id="_2-2-请求处理流程" tabindex="-1"><a class="header-anchor" href="#_2-2-请求处理流程"><span>2.2 请求处理流程</span></a></h3>
